import { promises as fs } from "fs";
import { Job } from "@/types/jobs";
import Image from "next/image";
import Filters from "@/components/jobs/filters";
import { Filter } from "@/types/filter";
import { Company } from "@/types/company";
import { JSX } from "react";

export default async function Jobs() {
  const data = await fs.readFile(
    process.cwd() + "/src/app/data/jobs.json",
    "utf-8"
  );

  const jobs: Job[] = JSON.parse(data);
  const filters: Filter = {
    location: [],
    company: [],
    employmentType: [],
    map: function (
      arg0: (item: Company | string) => JSX.Element
    ): import("react").ReactNode {
      throw new Error("Function not implemented.");
    },
  };

  if (!jobs || jobs.length === 0) {
    return <div>No jobs found</div>;
  }

  jobs.forEach((job) => {
    filters.company.push(job.company);
    filters.employmentType.push(job.description.employmentType);
    filters.location.push(job.location);
  });

  return (
    <div className="w-full flex flex-col items-center justify-center">
      <Filters filter={filters} />
      <div className="w-full flex flex-row flex-wrap items-center justify-center">
        {jobs.map((job: Job) => {
          return (
            <div
              key={job.id}
              className="p-5 rounded-xl shadow-md border-1 border-slate-200 min-w-1/3 max-w-3/4 m-2"
            >
              <div className="flex items-center justify-between">
                <div>
                  <h2 className="text-xl font-bold">{job.title}</h2>
                  <p>{job.company.name}</p>
                </div>
                <div>
                  <Image
                    src={job.company.logo ?? "/default-logo.png"}
                    alt={job.company.name}
                    width={50}
                    height={50}
                    className="rounded-full"
                  />
                </div>
              </div>
              <p>{job.location}</p>
              <p>{job.description.role}</p>
              <p>{job.description.industry}</p>
              <p>{job.description.employmentType}</p>
            </div>
          );
        })}
      </div>
    </div>
  );
}
